import http from '../utils/request'

//请求首页数据的接口
export const getData = () => {
    // 返回promise对象
    return http({
        url: '/home/getData',
        method: 'get',
        // // params: data,//get传递的数据
        // data:{},//post传递的数据
        // headers: {},//自定义请求头
        // timeout: 1000,//设置超时时间
        // responseType: '',//响应数据类型//默认json
    })
}

//请求用户数据的接口
export const getUser = (params) => {
    // console.log(params);
    // 返回promise对象
    return http({
        url: '/user/get',
        method: 'get',
        params: params,//get传递的数据
        // data:{},//post传递的数据
        // headers: {},//自定义请求头
        // timeout: 1000,//设置超时时间
        // responseType: '',//响应数据类型//默认json
    })
}

//请求用户数据的接口
export const createUser = (data) => {
    // 返回promise对象
    return http({
        url: '/user/add',
        method: 'post',
        // params: params,//get传递的数据
        data: data,//post传递的数据
        // headers: {},//自定义请求头
        // timeout: 1000,//设置超时时间
        // responseType: '',//响应数据类型//默认json
    })
}

