import axios from "axios";

const http = axios.create({
    baseURL: '/api',
    timeout: 10000,
})

//设置请求拦截器(用的较少)
//token校验；接受服务器返回的token，储存到vuex或者本地储存，每一次向服务器发送请求，我们应该把token带上
http.interceptors.request.use((config) => {
    // config.headers['token'] = getToken('token') //每一次向服务器发送请求，我们应该把token带上 
    return config
}, error => {
    return Promise.reject(error)
})


//响应拦截器
//服务器返回数据 -> [拦截的统一处理] -> 客户端获取信息

// axios.defaults.validateStatus = status =>{
//     return /^(2|3)\d{2}$/.test(status)
// }（一般不用）
http.interceptors.response.use(response => {
    // let { status, message } = response.data // 解构赋值
    return response  //拿到响应主体

}, error => {
    let { response } = error
    if (response) {
        //服务器至少返回结果了
        switch (response.status) {
            case 401: //权限
                break;
            case 403: //服务器拒绝
                break;
            case 404: //找不到页面
                break;

        }
    }
    else {
        //服务器连结果都没返回
        if (!window.navigator.onLine) {
            //断网处理：可以跳转到断网页面
            return
        }
        return Promise.reject(error)
    }
})

export default http